<template>
  <div id="footer-nav" v-show="bottomNavIsShow">
      <ul> 
        <li @click="switchTo(1)">精选</li>
        <li @click="switchTo(2)">我的</icon></li>
      </ul>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  name: 'BottomNav',
  methods : {
    switchTo(index){
      switch(index.toString()){
        case '1':
          this.$router.push('/');
          break;
        case '2':
          this.$router.push('/Mine');
          break;
      }
    },
  },
  computed: {
    ...mapGetters([
      'activeNavCurrent',
      'bottomNavIsShow',
    ])
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#footer-nav{
  width: 100%;
  height: 48px;
  -webkit-box-shadow:inset 0 0 1px #282f37;  
  -moz-box-shadow:inset 0 0 1px #282f37;  
  box-shadow:inset 0 0 1px #282f37; 
  background-color: #3f4852;
  position: fixed;
  bottom: 0;
  z-index: 2;
}

#footer-nav ul{
  width: 100%;
  height: 100%;
}

#footer-nav ul li{
  width: 50%;
  height: 100%;
  line-height: 48px;
  float: left;
  text-align: center;
}

#footer-nav ul li .active{
  color: #f7f7f7;
}
</style>



